import React, { useState, useEffect, useContext } from 'react';
import {ReducerContext} from '../store';
import axios from "axios";
import {Notification} from "../notification/notification";

export function General (props){

    const { state, dispatch } = useContext(ReducerContext);

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [title, setTitle] = useState('');
    const [text, setText] = useState('');
    
    const CheckValidation = () => {
        let pattern  = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if(!pattern .test(email)){
            dispatch({type: 'SHOW_NOTIFICATION', message: '  '});
            return false;
        }

        if(name === '' || email === '' || title === '' || text === '' || name === ' ' || email === ' ' || title === ' ' || text === ' '){
            dispatch({type: 'SHOW_NOTIFICATION', message: '   '});
            return false;
        }
        
        return true;
    }
    
    const Clear = () => {
        setName('');
        setEmail('');
        setTitle('');
        setText('');
    }
    
    const SendMassage = () => {
        
        if(!CheckValidation()){
            return;
        }
        
        axios({
            method: 'post',
            url: '/General/Massage',
            headers: { 'Content-Type': 'application/json' },
            data: {
                email: email,
                text: text,
                title: title,
                name: name
            }
        })
            .then(function (response) {
                dispatch({type: 'SHOW_NOTIFICATION', message: '  '});
                Clear();
            })
            .catch(function (error) {
                dispatch({type: 'SHOW_NOTIFICATION', message: ' - '});
                console.log(error.response.data);
            });
    }

    function onTitleChange(value){
        value = value.replace(/\s+/g, ' ');

        setTitle(value);
    }

    function onNameChange(value){
        value = value.replace(/\s+/g, ' ');

        setName(value);
    }

    function onTextChange(value){
        value = value.replace(/\s+/g, ' ');

        setText(value);
    }

    function onEmailChange(value){
        value = value.replace(/\s+/g, ' ');

        setEmail(value);
    }

    return (
        <div className='general'>
            <Notification />
            <div className='start-block'>
                <h1> </h1>
                <div className='img'>
                    <img src="../../images/general-start-img.png" alt="general img"/>
                </div>
                <p>     </p>
                <button onClick={() => window.location.href = "./Home/Notes"}> </button>
            </div>
            <div className='info-blocks'>
                <div className='up-info'>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/notes.png" alt="notes"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/diary.png" alt="diary"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/schedules.png" alt="schedules"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/statistic.png" alt="schedules"/>
                        </div>
                        <p>  </p>
                    </div>
                </div>
                <div className='down-info'>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/finances.png" alt="finances"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/targets.png" alt="targets"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/skills.png" alt="skills"/>
                        </div>
                        <p>  </p>
                    </div>
                    <div className='info-block'>
                        <div className='info-img'>
                            <img src="../../images/search.png" alt="schedules"/>
                        </div>
                        <p>  </p>
                    </div>
                </div>
            </div>
            <div className='description'>
                <h2></h2>
                <p>
                           . 
                           .
                             .
                           .
                          ,        .
                </p>
            </div>
            <div className='feedback-form'>
                <div className='form-content'>
                    <h2>  </h2>
                    <div className='form-field'>
                        <input type="text" placeholder=' ' value={name} onChange={e => onNameChange(e.target.value)}/>
                    </div>
                    <div className='form-field'>
                        <input type="text" placeholder=' ' value={email} onChange={e => onEmailChange(e.target.value)}/>
                    </div>
                    <div className='form-field'>
                        <input type="text" placeholder='' value={title} onChange={e => onTitleChange(e.target.value)}/>
                    </div>
                    <textarea placeholder='  ' value={text} onChange={e => onTextChange(e.target.value)}/>
                    <div>
                        <button on onClick={SendMassage}></button>
                    </div>
                </div>
            </div>
        </div>
    );
}